import { Component, OnInit ,Input, Output, EventEmitter } from '@angular/core';
import { UserService } from '../../../../../services/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup,  FormBuilder,  Validators ,FormControl} from '@angular/forms';
import { JobService } from '../../../../../services/job.service';
//import { PasswordValidator } from '../../../validator/password.validator';
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from '../../../../../../environments/environment';
import { Title } from '@angular/platform-browser';
class ImageSnippet {
	pending: boolean = false;
	status: string = 'init';
	constructor(public src: string, public file: File) {
		
	}
}

@Component({
  selector: 'app-register',
  templateUrl: 'viewbid.component.html',
  styleUrls : [ './viewbid.component.css' ]
})
export class ViewBidomponent implements OnInit{
	angForm: FormGroup;
	
	@Input() rating: number;
	@Input() itemId: number;
	@Output() ratingClick: EventEmitter<any> = new EventEmitter<any>();
  
	first_show = false;val:any='';
	allJobs:any="";jobservices:any="";allservice:any="";
	selectedFile: ImageSnippet;
	jobid = '';	appliances:any='';basements:any='';	diningrooms:any='';bedrooms:any='';
	familyrooms:any='';garages:any='';kitchens:any='';livingrooms:any='';patioyards:any='';
	userslist:any='';	jobData:any='';thisisdone:any="";alllocations:any="";
	states:any = '';counties:any = '';cities:any = '';allappliances:any='';
	show_dialog : boolean = false;
	button_text:any='';
	submitted:any='';bidData:any='';docUrl:any='';attachment_pic:any='';user_id:any='';
	movingData:any='';
	packingData:any='';
	unpackingData:any='';
	loadingData:any='';
	unloadingData:any=''; 
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private router: Router,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder,private titleService: Title)
	{
		this.makeBidForm();
		this.titleService.setTitle( 'View Bid | MoveLaunch' );
	}
	makeBidForm() {
		this.angForm = this.fb.group({
			// name: ['',  Validators.required ],
			bidAmount: ['', Validators.required ],
			bidProposal: ['', Validators.required ],
			imageInput: ['']
		}); 
	}
	private onSuccess() {
		this.selectedFile.pending = false;
		this.selectedFile.status = 'ok';
	}

	private onError() {
		this.selectedFile.pending = false;
		this.selectedFile.status = 'fail';
		this.selectedFile.src = '';
	}

	processFile(imageInput: any) {
		const file: File = imageInput.files[0];
		const reader = new FileReader();

		reader.addEventListener('load', (event: any) => {

		  this.selectedFile = new ImageSnippet(event.target.result, file);
		  this.selectedFile.pending = true;
		});
	
		reader.readAsDataURL(file);
		
	}
	
	getSavedJob(){
		var user_id = localStorage.getItem('userid');
		localStorage.setItem('jobType','saved');
		
		this.jobid = this.route.snapshot.paramMap.get('jobid');

		this.jobservice.getEditJob(this.jobid).subscribe(result => {
		/* this.createFromLocationForm(); */
			if(result.success){
				
				this.jobData = result.job;
				//this.jobData.servicename = this.jobData.serviceId.name;
				if(result.job.moving && result.job.moving.length > 0){
					this.movingData = result.job.moving[0];
				}
				console.log(this.movingData);
				if(result.job.packing && result.job.packing.length > 0){
					this.packingData = result.job.packing[0];
				}
				if(result.job.unpacking && result.job.unpacking.length > 0){
					this.unpackingData = result.job.unpacking[0];
				}
				if(result.job.loading && result.job.loading.length > 0){
					this.loadingData = result.job.loading[0];
				}
				if(result.job.unloading && result.job.unloading.length > 0){
					this.unloadingData = result.job.unloading[0];
				} 
				
				this.userservice.getStates().subscribe(result => {
					this.states = result;
				});
				var currentState = this.jobData.state;
				var currentCounty = this.jobData.county;
			
				this.userservice.getCounties(currentState).subscribe(result => {
					this.counties = result;
				}); 
					
				this.userservice.getCities(currentCounty,currentState).subscribe(result => {
					this.cities = result; 
				});
				
				//console.log(this.living);
			  }else{
				  this._flashMessagesService.show("No Saved Job Found.", { cssClass: 'alert-danger',timeout:6000});
			  } 
		});
	//	http://18.188.2.226:3001/api/partner/editbid/5cd518c538bc5c24ab886e43
		this.jobservice.editJobBids(this.jobid).subscribe( result=>{
			console.log(result);
			this.bidData = result.bid;
			var user_id = localStorage.getItem('userid');
		
			
			//this.docUrl  = environment.bid_document_url+this.user_id+'/'+this.jobid+'/'+result.bid.attachment_pic;
			this.docUrl  = result.bid.attachment_pic;
		});
	}
	get f() { return this.angForm.controls; }
	
	onSubmit(bidId) {
        this.submitted = true;
		
		if(this.angForm.invalid){
			 return;
        }else{
			var bid_info = this.angForm.value;
			var _userid = localStorage.getItem('userid');
			
			this.jobservice.updateBid(bid_info,bidId,_userid).subscribe(result =>
			{
				 if(result.success){
					this._flashMessagesService.show('Your bid has been updated successfully.', { cssClass: 'alert-success',timeout:5000}); 
					setTimeout(() => {           // or do some API calls/ Async events
						this.router.navigate(['/open-bids']);
					},2500);
				  }else{
					  this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:5000});
				  } 
			   
			});  
			
		}	
	}
	deleteJob(jobID){
		this.jobservice.deleteJob(jobID).subscribe(result =>
		{
			if(result.success){
				setTimeout(() => {           // or do some API calls/ Async events
					this.router.navigate(['/open-bids']);
				},2500);
				this._flashMessagesService.show("job has been deleted successfully", { cssClass: 'alert-success',timeout:6000});
			}else{
				this._flashMessagesService.show("Error in Creating Job", { cssClass: 'alert-danger',timeout:6000});
			} 
		   
		}); 
	}
	toggle() {
		this.show_dialog = !this.show_dialog;
		this.button_text = this.show_dialog ? 'View Less' : 'View More';
	}

	ngOnInit() {
		this.first_show = true;
		this.getSavedJob();
		this.button_text = 'View More';
	}

}
