import { Component, OnInit,Inject } from '@angular/core';
import { UserService } from '../../../../../services/user.service';
import { FormGroup,  FormBuilder,  Validators ,FormControl} from '@angular/forms';
import { JobService } from '../../../../../services/job.service';
//import { PasswordValidator } from '../../../validator/password.validator';
import { FlashMessagesService } from 'angular2-flash-messages';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {MatDialogModule} from '@angular/material/dialog';
import { Title } from '@angular/platform-browser';


export interface DialogData {
  animal: string;
  name: string;
  jobID:any;
}

@Component({
  selector: 'app-register',
  templateUrl: 'joblisting.component.html',
  styleUrls : [ './joblisting.component.css' ]
})
export class ListingJobComponent implements OnInit{
	
	first_show = false;
	val:any='';
	allJobs:any="";
	jobservices:any="";
	allservice:any="";
	isValid : boolean = false;
	name:any='';
	animal:any='';
	jobidisset:any='';
	message:any='';
	constructor(private _flashMessagesService: FlashMessagesService,private userservice: UserService,private jobservice: JobService, 
		private fb: FormBuilder,public dialog: MatDialog,private titleService: Title)
	{
		 this.getAllJobs();
		 this.titleService.setTitle( 'All Jobs | MoveLaunch' );
	}
	
	openDialog(jobId): void {
		const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
		  width: '450px',
		  data: {jobID:jobId}
		});

		dialogRef.afterClosed().subscribe(result => {
			if(result == 'Success'){
				this._flashMessagesService.show('job has been deleted successfully', { cssClass: 'alert-success',timeout:6000});
			}
			
		});
	  }
	  
	getAllJobs(){
		this.jobservice.getAllJobs().subscribe(result =>
		{
			this.isValid = true;
			if(result.success){
				 var d = new Date();
				var month = '' + (d.getMonth() + 1);
				var day = '' + d.getDate();
				var year = d.getFullYear();
				if (month.length < 2) month = '0' + month;
				if (day.length < 2) day = '0' + day;
				var mindateset = [year, month, day].join('-');  
				
				this.allJobs = result.job;
				this.allJobs.forEach((x,key) => {
					
					var d1 = new Date(x.jobStart);
					var month1 = '' + (d1.getMonth() + 1);
					var day1 = '' + d1.getDate();
					var year1 = d1.getFullYear();
					if (month1.length < 2) month1 = '0' + month1;
					if (day1.length < 2) day1 = '0' + day1;
					var mindateset1 = [year1, month1, day1].join('-');
					if(mindateset1 < mindateset){
						result.job[key].jobStatus =  'Old';
					}else{
						result.job[key].jobStatus =  'New';
					}
				});
				
				console.log(this.allJobs);
			}else{
				this.isValid = true;
				this._flashMessagesService.show("Error in Creating Job", { cssClass: 'alert-danger',timeout:6000});
			} 
		   
		}); 
	}
	
	ngOnInit() {
		this.first_show = true;
		this.isValid = false;
	}
}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-overview-example-dialog.html',
  styleUrls : [ './dialog-overview-example-dialog.css' ]
})
export class DialogOverviewExampleDialog {
	
	public jobID:any='';
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,  @Inject(MAT_DIALOG_DATA) public data: DialogData,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder) {
		
		this.jobID = data.jobID;
	}

	onNoClick(): void {
		this.dialogRef.close('ignore');
	}
  onYesClick(): void {
		this.jobservice.deleteJob(this.jobID).subscribe(result =>
		{
			if(result.success){
				let myElement = document.getElementsByClassName('myclass_'+this.jobID) as HTMLCollectionOf<HTMLElement>;
				myElement[0].style.display = "none";
				this.dialogRef.close('Success');
			}
		   
		});  
  }

}
