@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-overview-example-dialog.html',
  styleUrls : [ './dialog-overview-example-dialog.css' ]
})
export class DialogOverviewFeedback {
	public jobID:any='';public reviews:any='';public width:any='';
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewFeedback>,  @Inject(MAT_DIALOG_DATA) public data: DialogData,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder) {
		this.jobID = data.jobID;
		this.getFeedback(this.jobID);
	}
	getFeedback(jobID){
		this.jobservice.getJobFeedbackForUser(jobID).subscribe(result=>{
			this.reviews = result.review;
			this.width = result.review.rating*20;
		});
	}
	closeDialog(): void {
		this.dialogRef.close();
	}

}
