addDropDowns(serviceId,formtype,ser_type,saved_data) {
		var setApp = [];
	
	   	this.jobservice.getAppliances(serviceId).subscribe(result => {
			this.apppli_moving = '';
			this.formtype = formtype;
			this.formtype.reset({});
			this.formtype.setControl('allappliances', this.fb.array([]));
			this.allappliances = result;
			this.allappliances.map((o, i) => {
				const control = new FormControl(i === 0); // if first item set to true, else false
				(this.formtype.controls.allappliances as FormArray).push(control);
			});
			
			if(saved_data && saved_data.appliances && saved_data.appliances.length > 0){
				for(var i=0;i<this.allappliances.length;i++ ){
					setApp.push('null');
					var jobdata = saved_data.appliances;
					var _id = this.allappliances[i]._id;
					$.each(jobdata,function(index,value){
						if(value.applianceItem._id == _id){
							var item = value.items+'_'+value.applianceItem._id;
							setApp[i] = item;
						}
					});  		
				}
				
			}else{
				for(var i=0;i<this.allappliances.length;i++ ){
					setApp.push('null');
				}
			}
			
			if(ser_type == 'moving'){
				this.apppli_moving = setApp;
			}
			if(ser_type == 'packing'){
				this.apppli_packing = setApp;
			}
			if(ser_type == 'unpacking'){
				this.apppli_unpacking = setApp;
			}
			if(ser_type == 'loading'){
				this.apppli_loading = setApp;
			}
			if(ser_type == 'unloading'){
				this.apppli_unloading = setApp;
			}
		
			
		}); 
		
	}
	
