	/*---------------------------------- Get saved job --------------------------------*/
	
	public geSavedJob(whichstep){
		var user_id = localStorage.getItem('userid');
		localStorage.setItem('jobType','saved');
		
		this.showLoader = true;
		this.jobservice.getSavedJob().subscribe(result => {
			if(result.success && result.message.length > 0){
				this.showLoader = false;
				//http://localhost:4200/edit-job/5ea8fd1593c4153e8010841c
				this.selectedServices = result.message[0].serviceId;
				
				
				this.jobData = result.message[0];
				this.movingData = result.message[0].moving[0];
				this.packingData = result.message[0].packing[0];
				this.unpackingData = result.message[0].unpacking[0];
				this.loadingData = result.message[0].loading[0];
				this.unloadingData = result.message[0].unloading[0]; 
				
				if(this.movingData && whichstep == 'moving'){
					console.log('movingData'+this.movingData);
					this.showMoving = true;
					this.setLocations(this.secondFormGroup,this.movingData);	
					this.setToLocations(this.thirdFormGroup,this.movingData);
					
				}
				if(this.packingData && whichstep == 'packing'){
					console.log(this.packingData);
					this.showPacking = true;
					this.setLocations(this.secondpackedFormGroup,this.packingData);	
					this.setToLocations(this.thirdpackedFormGroup,this.packingData);	
					
				}	
				if(this.unpackingData && whichstep == 'unpacking'){
					this.showUnPacking = true;
					this.setLocations(this.secondunpackedFormGroup,this.unpackingData);	
					this.setToLocations(this.thirdunpackedFormGroup,this.unpackingData);	
					
				}	
				if(this.loadingData && whichstep == 'loading'){
					this.showLoading = true;
					this.setLocations(this.secondloadingFormGroup,this.loadingData);	
					
				}	
				if(this.unloadingData &&  whichstep == 'unloading'){	
				
					this.showUnLoading = true;
					this.setToLocations(this.thirdunloadingFormGroup,this.unloadingData);	
					
				}
								
				this.userservice.getStates().subscribe(result => {
					this.states = result;
				});
				var currentState = this.jobData.state;
				var currentCounty = this.jobData.county;
			
				this.userservice.getCounties(currentState).subscribe(result => {
					this.counties = result;
				}); 
					
				this.userservice.getCities(currentCounty,currentState).subscribe(result => {
					this.cities = result; 
				});
				
			
			  }else{
				  this._flashMessagesService.show("No Saved Job Found.", { cssClass: 'alert-danger',timeout:6000});
			  }  
		});
	}
	
