	submit_final_job(stepper,final_submit,step,jobID,serviceType){
		console.log('sdfsdf'+serviceType);
		this.showLoader = true;
		this.jobservice.updateJob(final_submit,step,jobID,serviceType).subscribe(result =>
		{
			var flashMessagesService = this._flashMessagesService;
			 this.submitted_10 = false;
			if(result.success){
				
					if(step != 'step3' && step != 'step4' ){
						
						this.jobservice.getSavedJob().subscribe(result => {
							this.jobData = result.message[0];
							if(result.message[0].moving.length > 0){
								this.movingData = result.message[0].moving[0];
							}
							
							if(result.message[0].packing.length > 0){
								this.packingData = result.message[0].packing[0];
							}
							if(result.message[0].unpacking.length > 0){
								this.unpackingData = result.message[0].unpacking[0];
							}
							if(result.message[0].loading.length > 0){
								this.loadingData = result.message[0].loading[0];
							}
							if(result.message[0].unloading.length > 0){
								this.unloadingData = result.message[0].unloading[0];
							}
						});
					}
					
				this.showLoader = false;
				 setTimeout(() => {           // or do some API calls/ Async events
				  stepper.next();
				 }, 1);
			}else{
				  flashMessagesService.show("Error in Creating Job", { cssClass: 'alert-danger',timeout:6000});
			}  
		   
		});  
		
	}
	setupJob(stepper: MatStepper){
		this.showLoader = true;
		this.jobservice.submitJob().subscribe(result =>
			{	
				var flashMessagesService = this._flashMessagesService;
				if(result.success){
						setTimeout(() => {           // or do some API calls/ Async events
						  stepper.next();
						 }, 1);
					 this.showLoader = false;
					 flashMessagesService.show("Job Created Successfully", { cssClass: 'alert-success',timeout:2000});
					setTimeout(() => {           // or do some API calls/ Async events
						this.router.navigate(['/jobs']);
					 }, 2500);
				}else{
					flashMessagesService.show("Error in Creating Job", { cssClass: 'alert-danger',timeout:6000});
				} 
			   
			});  
	}
