	public onStepperSelectionChange(event: any) {
		this.scrollToSectionHook(); 
	}
	private scrollToSectionHook() {
		const element = document.querySelector('.stepperTop'); 
		if (element) { 
			setTimeout(() => {
				element.scrollIntoView({behavior: 'smooth', block: 'start', inline: 'nearest'});
			}, 0 );
		}
	} 
	
	checkName(servicename){
		this.submitted_service = false;
	}
	checkOptions(servicename){
		if(servicename == 'moving'){
			
				this.showErrorMessage1 = false;
		}
		if(servicename == 'packing'){
				this.showErrorMessage2 = false;
			
		}
		if(servicename == 'unpacking'){
				this.showErrorMessage3 = false;
			
		}
		if(servicename == 'loading'){
				this.showErrorMessage4 = false;
			
		}
		if(servicename == 'unloading'){
			this.showErrorMessage5 = false;
			
		}
		
	}
