import { Component, OnInit ,Input, Output, EventEmitter } from '@angular/core';
import { UserService } from '../../../../../services/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup,  FormBuilder,  Validators ,FormControl} from '@angular/forms';
import { JobService } from '../../../../../services/job.service';
//import { PasswordValidator } from '../../../validator/password.validator';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Title } from '@angular/platform-browser';
class ImageSnippet {
	pending: boolean = false;
	status: string = 'init';
	constructor(public src: string, public file: File) {
		
	}
}

@Component({
  selector: 'app-register',
  templateUrl: 'editfeedback.component.html',
  styleUrls : [ './editfeedback.component.css' ]
})
export class EditFeedbackComponent implements OnInit{
	angForm: FormGroup;
	
	@Input() rating: number;
	@Input() itemId: number;
	@Output() ratingClick: EventEmitter<any> = new EventEmitter<any>();
  
	first_show = false;val:any='';
	allJobs:any="";jobservices:any="";allservice:any="";
	selectedFile: ImageSnippet;
	reviewid = '';	
	jobfeedback = '';	
	
	userslist:any='';	jobData:any='';thisisdone:any="";alllocations:any="";
	states:any = '';counties:any = '';cities:any = '';allappliances:any='';
	show_dialog : boolean = false;
	button_text:any='';
	submitted:any='';inputName: string;ratingClicked:any='';
	 
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private router: Router,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder,private titleService: Title)
	{
		this.makeBidForm();
		this.getFeedback();
		this.titleService.setTitle( 'Edit Feedback | MoveLaunch' );
	}
	makeBidForm() {
		this.angForm = this.fb.group({
			// name: ['',  Validators.required ],
			ratingSelected: ['', Validators.required ],
			jobFeedback: ['', Validators.required ],
		}); 
	}
	
	getFeedback(){
		var user_id = localStorage.getItem('userid');
	
		this.reviewid = this.route.snapshot.paramMap.get('reviewid');
		console.log(this.reviewid);
		this.jobservice.getUserFeedback(this.reviewid).subscribe(result => {
		/* this.createFromLocationForm(); */
			if(result.success){
				this.rating = result.review.rating;
				this.ratingClicked = result.review.rating;
				this.jobfeedback = result.review.feedback;
			}else{
				 this._flashMessagesService.show("No Review Found.", { cssClass: 'alert-danger',timeout:6000});
			} 
		});
		/* this.jobservice.getJobBids(this.jobid).subscribe(result=>{
		
			
			$.each(result.users, function(index, value) { 		
				result.users[index].width = value.userRating*10;
			});	
			this.userslist = result.users;
			console.log(this.userslist);
		}); */
	}
	onClick(rating: number): void {
		this.rating = rating;
		this.ratingClick.emit({
		  itemId: this.itemId,
		  rating: rating
		});
		this.ratingClicked  = this.rating;
	}
	get f() { return this.angForm.controls; }
	onSubmit() {
        this.submitted = true;
		
		if(this.angForm.invalid){
			 return;
        }else{
			
			var rating_info = this.angForm.value;
			this.reviewid = this.route.snapshot.paramMap.get('reviewid');
			this.jobservice.updateReviews(rating_info,this.reviewid).subscribe(result =>
			{
				
				 if(result.success){
					this._flashMessagesService.show('Your review has been submit successfully.', { cssClass: 'alert-success',timeout:5000}); 
				  }else{
					  this._flashMessagesService.show(result.message, { cssClass: 'alert-danger',timeout:5000});
				  } 
			   
			}); 
		 	 
		}	
	}
	deleteJob(jobID){
		this.jobservice.deleteJob(jobID).subscribe(result =>
		{
			if(result.success){
				this._flashMessagesService.show("job has been deleted successfully", { cssClass: 'alert-success',timeout:6000});
			}else{
				this._flashMessagesService.show("Error in Creating Job", { cssClass: 'alert-danger',timeout:6000});
			} 
		   
		}); 
	}
	toggle() {
		this.show_dialog = !this.show_dialog;
		this.button_text = this.show_dialog ? 'View Less' : 'View More';
	}

	ngOnInit() {
		this.inputName = this.itemId + '_rating';
		this.first_show = true;
		
		this.button_text = 'View More';
		this.ratingClicked= '';
	}

}
