import { Component, OnInit ,Input, Output, EventEmitter,Inject } from '@angular/core';
import { UserService } from '../../../../../services/user.service';
import { ActivatedRoute, Router,NavigationEnd } from '@angular/router';
import { FormGroup,  FormBuilder,  Validators ,FormControl} from '@angular/forms';
import { JobService } from '../../../../../services/job.service';
//import { PasswordValidator } from '../../../validator/password.validator';
import { FlashMessagesService } from 'angular2-flash-messages';
import { environment } from '../../../../../../environments/environment';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {MatDialogModule} from '@angular/material/dialog';
import { Title } from '@angular/platform-browser';
export interface DialogData {
	jobID:any;type:any;userid:any;
}




@Component({
  selector: 'app-register',
  templateUrl: 'jobdetails.component.html',
  styleUrls : [ './jobdetails.component.css' ]
})
export class DetailsJobComponent implements OnInit{
	@Input() rating: number;
	@Input() itemId: number;
	@Output() ratingClick: EventEmitter<any> = new EventEmitter<any>();
  
	first_show = false;val:any='';
	allJobs:any="";jobservices:any="";allservice:any="";
	
	jobid = '';	appliances:any='';basements:any='';	diningrooms:any='';bedrooms:any='';
	familyrooms:any='';garages:any='';kitchens:any='';livingrooms:any='';patioyards:any='';
	userslist:any='';	jobData:any='';thisisdone:any="";alllocations:any="";
	states:any = '';counties:any = '';cities:any = '';allappliances:any='';
	show_dialog : boolean = false;
	button_text:any='';image_url:any='';hire_decline:any='';decline_hire:any='';
	 movingData:any='';
	packingData:any='';
	unpackingData:any='';
	loadingData:any='';
	unloadingData:any='';
	
	constructor(private _flashMessagesService: FlashMessagesService,private route: ActivatedRoute,private router: Router,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder,public dialog: MatDialog,private titleService: Title)
	{
		this.getSavedJob();
		this.titleService.setTitle( 'Job Detail | MoveLaunch' );
		
		
		this.router.routeReuseStrategy.shouldReuseRoute = function(){
			return false;
		}
		this.router.events.subscribe((evt) => {
			if (evt instanceof NavigationEnd) {
			   // trick the Router into believing it's last link wasn't previously loaded
			   this.router.navigated = false;
			   // if you need to scroll back to top, here is the right place
			   window.scrollTo(0, 0);
			}
		});
	}
	openDialog(type,jobID,userid): void {
		const dialogRef = this.dialog.open(HireDeclineDialog, {
		  width: '450px',
		  data: {jobID:jobID,type:type,userid:userid}
		});

		dialogRef.afterClosed().subscribe(result => {
			
			if(result == 'Success'){
				this._flashMessagesService.show('job has been deleted successfully', { cssClass: 'alert-success',timeout:6000});
			}
			console.log(result);
			if(result == 'Hired'){
				this.hire_decline = this.hire_decline ? 'Hired' : 'Hire';
				this.decline_hire = this.decline_hire ? 'Decline' : 'Declined';
				
				setTimeout(() => {           // or do some API calls/ Async events
					this.router.navigate(['/user-in-progress-jobs']);
				},1200);
			}
			if(result == 'Declined'){
				this.decline_hire = this.decline_hire ? 'Declined' : 'Decline';
				this.hire_decline = this.decline_hire ? 'Hire' : 'Hired';
			}
			
		});
	  }
	  
	getUserListonJob(){
		this.jobid = this.route.snapshot.paramMap.get('jobid');
		
		this.jobservice.getJobBids(this.jobid).subscribe(result=>{
			$.each(result.users, function(index, value) { 		
				result.users[index].width = value.userRating*20;
				if(value.profile_pic){
					result.users[index].image_url  = environment.partner_image_url+value._id+'/'+value.profile_pic;
				}	
			});	 
			console.log(result);
			this.userslist = result.users;
			
		});
	}
	getSavedJob(){
		var user_id = localStorage.getItem('userid');
		localStorage.setItem('jobType','saved');
		
		this.jobid = this.route.snapshot.paramMap.get('jobid');
		this.jobservice.getEditJob(this.jobid).subscribe(result => {
			console.log(result);
		/* this.createFromLocationForm(); */
			if(result.success){
				
				this.jobData = result.job;
				//this.jobData.servicename = this.jobData.serviceId.name;
				 
				if(result.job.moving && result.job.moving.length > 0){
					this.movingData = result.job.moving[0];
				}
				console.log(this.movingData);
				if(result.job.packing && result.job.packing.length > 0){
					this.packingData = result.job.packing[0];
				}
				if(result.job.unpacking && result.job.unpacking.length > 0){
					this.unpackingData = result.job.unpacking[0];
				}
				if(result.job.loading && result.job.loading.length > 0){
					this.loadingData = result.job.loading[0];
				}
				if(result.job.unloading && result.job.unloading.length > 0){
					this.unloadingData = result.job.unloading[0];
				}
				this.getUserListonJob();
			  }else{
				  this._flashMessagesService.show("No Saved Job Found.", { cssClass: 'alert-danger',timeout:6000});
			  } 
		});
		
		 
	}
	
	deleteJob(jobID){
		this.jobservice.deleteJob(jobID).subscribe(result =>
		{
			if(result.success){
				this._flashMessagesService.show("job has been deleted successfully", { cssClass: 'alert-success',timeout:6000});
			}else{
				this._flashMessagesService.show("Error in Creating Job", { cssClass: 'alert-danger',timeout:6000});
			} 
		   
		}); 
	}
	
	toggle() {
		this.show_dialog = !this.show_dialog;
		this.button_text = this.show_dialog ? 'View Less' : 'View More';
	}

	ngOnInit() {
		this.first_show = true;
		this.jobid = this.route.snapshot.paramMap.get('jobid');
		this.button_text = 'View More';
		this.hire_decline = 'Hire';
		this.decline_hire = 'Decline';
	}

}
@Component({
  selector: 'dialog-hire-decline',
  templateUrl: 'dialog-hire-decline.html',
  styleUrls : [ './dialog-hire-decline.css' ]
})
export class HireDeclineDialog {
	
	public jobID:any='';
 	public type:any='';
 	public userid:any='';
 
  constructor(
    public dialogRef: MatDialogRef<HireDeclineDialog>,  @Inject(MAT_DIALOG_DATA) public data: DialogData,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder) {
		
		console.log(data);
		this.jobID = data.jobID;
		this.type = data.type;
		this.userid = data.userid;
	} 

	onNoClick(): void {
		this.dialogRef.close('ignore');
	}
	onYesClick(type_sent): void {
	 
		this.jobservice.hireDecline(type_sent,this.jobID,this.userid).subscribe(result =>
		{
			if(result.success && type_sent ==1){
				this.dialogRef.close('Hired');
			}
			if(result.success && type_sent ==2){
				this.dialogRef.close('Declined');
			} 
		   this.dialogRef.close('ignore');
		}); 
		
	}

}