import { Component, OnInit,Inject } from '@angular/core';
import { UserService } from '../../../../../services/user.service';
import { FormGroup,  FormBuilder,  Validators ,FormControl} from '@angular/forms';
import { JobService } from '../../../../../services/job.service';
//import { PasswordValidator } from '../../../validator/password.validator';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-register',
  templateUrl: 'joblistingpartner.component.html',
  styleUrls : [ './joblistingpartner.component.css' ]
})
export class ListingPartnersJobComponent implements OnInit{
	
	first_show = false;val:any='';
	allJobs:any="";jobservices:any="";allservice:any="";
	isValid : boolean = false;
	name:any='';animal:any='';jobidisset:any='';
	message:any='';
	constructor(private _flashMessagesService: FlashMessagesService,private userservice: UserService,private jobservice: JobService, private fb: FormBuilder,private titleService: Title)
	{
		this.titleService.setTitle( 'Jobs | MoveLaunch' );
		 this.getAllJobs();
	}
	
	 
	getAllJobs(){
		
		this.jobservice.getAllJobsForPartner().subscribe(result =>
		{
			if(result.success){
				this.isValid = true;
				var d = new Date();
				var month = '' + (d.getMonth() + 1);
				var day = '' + d.getDate();
				var year = d.getFullYear();
				if (month.length < 2) month = '0' + month;
				if (day.length < 2) day = '0' + day;
				var mindateset = [year, month, day].join('-');
				
				this.allJobs = result.jobs;
				console.log(this.allJobs);
				this.allJobs.forEach((x,key) => {
					this.jobservice.getBidsOnJob(x._id).subscribe(result1 =>
					{
						if(result1.success){
							this.allJobs[key].jobStatus =  'applied';
							
						}else{
							this.allJobs[key].jobStatus =  'Notapplied';
						}
					});
				}); 
				
				/* this.allJobs = result.jobs;*/	
				
			}else{
				this.isValid = true;
				//this._flashMessagesService.show("No job founds", { cssClass: 'alert-danger',timeout:6000});
			}   
		   
		});
			
	}
	getBidsOnJob(){
		console.log('dgdf');
		/* this.jobservice.getBidsOnJob(value_id).subscribe(result1 =>
		{
			console.log(result1);
		}); */ 
	}
	
	ngOnInit() {
		this.first_show = true;
		this.isValid = false;
		
	}
	

}
